nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff8e5;
    padding: 0px 80px;
    box-shadow: 0 5px 15px rgba(0,0,0,0.06);
}

#navbar {
    display: flex;
    align-items: center;
    justify-content: center;

}

#navbar li {
    list-style: none;
    padding: 0 20px;
    position: relative;
}

#navbar li a {
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: #4d162d;
    transition: 0.3s ease-in-out;

}

#navbar li a:hover,
#navbar li a.active {
    color: #114D34;
}

#navbar li a:hover::after,
#navbar li a.active::after  {
    content: "";
    width: 30%;
    height: 2px;
    background: #114D34;
    position: absolute;
    bottom: -4px;
    left: 20px;
}

#mobile {
    display: none;
}
#mobile i{
    color: #4d162d;

} 

@media screen and (max-width:769px){
    #navbar {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 68px;
        right:-300px;
        width: 300px;
        height: 100vh;
        background-color: #ecdccc;
        box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
        padding: 40px 0 0 0px;
        transition: 0.3s ease-in-out;
    }
    #navbar.active {
        right: 0px;
    }
    #navbar li {
        margin-bottom: 25px;
    }

    #mobile {
        display: block;
    }

    #mobile i {
        font-size: 24px;
        cursor: pointer;
    }
}